import React from "react";
import Carousel from "react-bootstrap/Carousel";

const CarouselComponent = () => {
  return (
    <Carousel style={{ height: "150px" }}>
      <Carousel.Item style={{ textAlign: "center" }}>
        <h4>Alan Walker</h4>
        <p style={{fontSize:'20px'}}>Excelente Servico.</p>
      </Carousel.Item>
      <Carousel.Item style={{ textAlign: "center" }}>
        <h3>Emilio Delgado</h3>
        <p style={{fontSize:'20px'}}>Est muy recomendado el servicio. Excelente!!</p>
      </Carousel.Item>
      <Carousel.Item style={{ textAlign: "center" }}>
        <h3>Mariano Delgado</h3>
        <p style={{fontSize:'20px'}}>Me resolvi el problema. Muchas gracias.</p>
      </Carousel.Item>
    </Carousel>
  );
};

export default CarouselComponent;
